
/**
 * ui模块
 * @type {{mutations: {updateContents(*, *): void}, state: (function(): {contents: [{extData: {domain: string, pwd: string, username: string}, name: string, href: string, type: string}]}), getters: {}}}
 */
const ui = {
    namespaced: true,
    state: () => ({
        openHistory: [],//打开历史，用于返回
        //列表项宽度
        itemSize: 120,
        filters:[],//过滤器
        config:{
          showSubFolders:true //是否显示子文件夹
        },
        //当前选项卡
        currentTab: {
            name: 'all',
            alias: '全部',
            path: ''
        },
        /**
         * 当前文件夹
         */
        currentFolder: {
            path: '',
            name: '我的图片'
        },
        /**
         * 当前选中的文件的信息
         */
        // selectedContentInfo: {
        //   cover: '',
        //   name: '名称名称',
        //   remark: '备注备注',
        //   href: 'http://apps.vip',
        //   rate: 4.5,
        //
        //   //基本信息
        //   baseInfo: {
        //     filename: '',
        //     path: '',
        //     createTime: Date.now(),
        //     modifyTime: Date.now(),
        //     addTime: Date.now(),
        //   }
        // }
    }),
    mutations: {
        setFilter(state,ft){
            state.filters=ft

        },
        addFilter(state,ft){
            state.filters.push(ft)
        },
        pushHistory(state, path) {
            state.openHistory.push(path)
        },
        popHistory(state) {
            state.openHistory.pop()
        },

        /**
         * 设置itemSize，列表内容宽度
         * @param state
         * @param size
         */
        setItemSize(state, size) {
            state.itemSize = size
        },
        /**
         * 设置当前选中的tab
         * @param state
         * @param tab
         */
        setTab(state, tab) {
            state.currentTab = tab
        },
        setCurrentFolder(state, folder) {
            state.currentFolder = folder
        },
        /**
         * 聚焦当前选中的项目
         * @param state
         * @param item
         */
        focusItem(state, item) {
            if(item.data.meta.new){
                item.data.meta.new=false
                window.libDb.setNotNew(item.data.meta.uuid)
            }
            state.focusedItem=item
        },
        setConfig(state,config){
            state.config=Object.assign(state.config,config)
        }
    },
    getters: {},
    actions: {
        init(context) {
            context.dispatch('subFolder/updateTreeData', null, {root: true})
        },
        goBackFolder(context) {
            if (context.state.openHistory.length <2) {
                return
            }
            let backPath=context.state.openHistory[context.state.openHistory.length-2]
            context.commit('popHistory')
            context.commit('popHistory')
            context.dispatch("changeFolder", backPath)
        },
        reload(context,callback){
            window.libDb.reload()
            context.dispatch('content/loadFolderContents',context.state.currentFolder.path,{root:true}) //读入文件夹内容
            if(callback){
                callback()
            }
        },
        changeFolder(context, folder) {
            context.dispatch('content/loadFolderContents',folder.path,{root:true}) //读入文件夹内容
            context.commit('setCurrentFolder', folder) //设置当前文件夹
            context.commit('pushHistory', folder) //往历史推内容
        },
        changeTab(context, tab) {
            switch (tab.name) {
                case 'all':
                    tab.alias = '全部'
                  context.commit('setFilter',[])
                    //todo 重新载入目录
                    context.dispatch('changeFolder', {
                        relativePath: '/',
                        path:context.rootState.config.storePath,
                        name: ''
                    })
                break
                case 'bookmark':
                    tab.alias = '书签'
                    //todo 重新载入目录
                break
                case 'img':
                    tab.alias='图片'
                break
                case 'video':
                    tab.alias='视频'
                    break
                case 'doc':
                    tab.alias='文档'
            }
            context.dispatch('changeFolder', {
                relativePath: '/',
                path:context.rootState.config.storePath,
                name: ''
            })
            context.commit('setTab', tab)
        }
    }
}

export default ui
