<template>
  <div v-if="subFolders.length>0">
    <h3 style="margin-bottom:10px">文件夹（{{ subFolders.length }}）
    </h3>
    <div style="min-height:150px">
      <a-dropdown :trigger="['contextmenu']"  v-for="folder in subFolders" v-bind:key="folder">
      <div class="sub-folder-wrapper" @click="selectItem(folder)" @dblclick="changePath(folder.name)" :style="{width:itemSize+'px',height:itemSize+50+'px'}" style="" >
        <div class="cover-wrapper" style="position: relative" :style="{width:itemSize+'px',height:itemSize+'px'}">
          <div class="sub-folder-frame" :style="{width:itemSize+'px',height:itemSize+'px'}" ></div>
          <div v-if="folder.meta.new" class="type-label new-label">New</div>
          <a-image style="background: #f6f6f6;padding: 20px" :style="{width:itemSize+'px',height:itemSize+'px'}" :preview="false" class="sub-folder-cover" :src="folder.cover">
          </a-image>
        </div>
        <div class="folder-name">{{folder.name}}</div>
        <div class="inner-content-count">{{folder.contentsCount}} 内容</div>
      </div>
      <template #overlay>
        <a-menu>
          <a-menu-item key="openPath" @click="open(folder.baseInfo.path+folder.baseInfo.filename)" >打开</a-menu-item>
          <a-menu-item key="find" @click="showItemInFolder(folder.baseInfo.path+folder.baseInfo.filename)">查看所在位置</a-menu-item>
          <a-menu-item key="delete" @click="trashItem(folder.baseInfo.path+folder.baseInfo.filename)">删除文件夹</a-menu-item>
       </a-menu>
      </template>
      </a-dropdown>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
import { Modal } from 'ant-design-vue'
export default {
  name: 'SubFolderList',
  props: {
    msg: String
  },
  computed:{
    ...mapState({
      subFolders:state=>state.subFolder.subFolders,
      itemSize:state=>state.ui.itemSize
    })
  },
  methods:{
    changePath(path){
      this.$store.dispatch('ui/changeFolder',{path:this.$store.state.ui.currentFolder.path + path+'/'})
    },
    selectItem(folder){
      this.$store.commit('ui/focusItem',{
        type:'folder',
        data:folder
      })
    },
    open:ipc.openExternal,
    showItemInFolder:ipc.showItemInFolder,
    trashItem(path){
      let that=this
      Modal.confirm({
        title: '删除确认',
        content: '是否确认删除文件夹？此操作会删除文件夹下面全部的内容。',
        okText: '确认',
        cancelText: '取消',
        onOk:()=>{
          ipc.trashFolder(path)
          setTimeout(()=>{
            that.$store.dispatch('ui/changeTab',{name:'all'})
          },200)
          this.$message.success('删除文件夹成功。')
        }
      });
    },
  }
}
</script>

