import contentListModel from "@/models/contentListModel";
import contentModel from "@/utils/model/contentModel";
import pathTools from "@/utils/pathTools";
import subFolderModel from '@/utils/model/subFolderModel'
const fs = eval('require')('fs')
/**
 * 团队模块
 * @type {{mutations: {updateContents(*, *): void}, state: (function(): {contents: [{extData: {domain: string, pwd: string, username: string}, name: string, href: string, type: string}]}), getters: {}}}
 */
const group = {
  namespaced:true,
  state: () => ({
    contents: []
  }),
  mutations: {
    /**
     * 更新当前显示的内容
     * @param state
     * @param contents
     */
    updateContents (state, contents) {
      state.contents = contents
    },
    assignContentMeta(state,meta){
      let foundContent=null
     state.contents=  state.contents.map((content)=>{
        if(pathTools.convertToCommonRelativePath(content.baseInfo.relativePath+content.baseInfo.filename)===meta.relative_path_full){
          content=contentModel.getContentFromFile(meta.relative_path_full)
          foundContent=content
        }
        return content
      })
      if(!foundContent){
        state.contents= state.contents.unshift(contentModel.getContentFromFile(meta.relative_path_full))
      }
    },
  },
  getters: {
    contentsOrderByCreateTime(state){
      function compare(property){
        return function(a,b){
          var value1 = a.baseInfo[property];
          var value2 = b.baseInfo[property];
          return  value2-value1;
        }
      }
      return state.contents.sort(compare('createTime'))
    }
  },
  actions:{
    loadFolderContents(context,path){
      //是本地，则进行本地文件的导入。
      let files = contentListModel.loadLocalStoreContents(path)
      let config=context.rootState.ui.config //获取ui的config
      let contents = []//内容
      let subFolders = []//目录
     let  filteredFiles=files
      //按照过滤条件进行一次过滤
      if(context.rootState.ui.filters.length>0){
        filteredFiles=files.filter(file=>{
         if(!file.stat.isFile()) return true
          let mapped=false
          //使用当前的过滤器去过滤一遍
          context.rootState.ui.filters.forEach(ft=>{
            if(ft(file)){
              mapped=true
            }
          })
          return mapped;
        })
      }
      filteredFiles.forEach(file => {
        let filename = file.filename
        if (filename.startsWith('.DS_Store'))
        {
          return
        }
        const fileInfo = fs.statSync(path + file.filename)
        if (fileInfo.isFile() && filename!=='_db.json') {
          let content =contentModel.getContentFromFile(pathTools.getRelativeFullPathFromFullPath(path+file.filename))
          contents.push(content)
        } else if (fileInfo.isDirectory() && config.showSubFolders) {
          //是目录，且当前配置显示子目录
          if(file.filename==='_assets') return
          let subFolder=subFolderModel.getSubFolderFromFolder(pathTools.getRelativeFullPathFromFullPath(path+file.filename))
          subFolders.push(subFolder)
        }
      })

      context.commit('content/updateContents', contents, {root: true})

      context.commit('subFolder/setCurrentSubFolders', subFolders, {root: true})
      let newFiles= window.libDb.scanLib(path)
      if(newFiles.length>0){
        window.app.$message.success({content:'扫描到此文件夹及子文件夹新增了 '+newFiles.length+' 个文件或文件夹。已为您标记为New，并更新到资源库中。'})
      }
    }
  }
}

export default group
