<template>
  <div class="hello">
    <h3 style="margin-bottom:10px;margin-top: 20px">内容（{{ contents.length }}）</h3>
    <div style="min-height:150px">
        <a-empty style="margin-top: 60px" description="此文件夹下无内容" v-if="contents.length===0"></a-empty>
        <a-dropdown v-else :trigger="['contextmenu']" v-for="content in contentsOrderByCreateTime" v-bind:key="content">
          <div class="sub-folder-wrapper" :class="{'selected':selectedKey===content}" @click="selectedKey=content"
               :style="{width:itemSize+'px',height:itemSize+50+'px'}" >
            <template v-if="content.extCanOpenInside">
              <TypePwd @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='pwd'" v-bind:content="content"/>
              <TypeImg @setSelected="selectItem(content)"  @openDetail="openDetail" v-if="content.type==='img'" v-bind:content="content"/>
              <TypeUrl @setSelected="selectItem(content)"  @openDetail="openDetail" v-if="content.type==='url'" v-bind:content="content"/>
              <TypeVideo @setSelected="selectItem(content)"  @openDetail="openDetail" v-if="content.type==='video'" v-bind:content="content"/>
              <div v-if="content.meta.new" class="type-label new-label">New</div>
            </template>
            <template v-else>
              <TypeOther @setSelected="selectItem(content)"  @openDetail="openPath" v-bind:content="content"/>
            </template>
          </div>
          <template #overlay>
            <a-menu>
              <a-menu-item key="openPath" @click="open(content.baseInfo.path+content.baseInfo.filename)" >打开</a-menu-item>
              <a-menu-item key="find" @click="showItemInFolder(content.baseInfo.path+content.baseInfo.filename)">查看所在位置</a-menu-item>
              <a-menu-item key="delete" @click="trashItem(content.baseInfo.path+content.baseInfo.filename)">删除文件</a-menu-item>
              <a-menu-item v-if="content.type==='img'" key="edit" @click="editPicture(content.baseInfo.path+content.baseInfo.filename)">编辑图片</a-menu-item>
              <a-menu-item v-if="content.type==='img'" key="setWallpaper" @click="setWallPaper(content.baseInfo.path+content.baseInfo.filename)">设为新标签页默认壁纸</a-menu-item>
            </a-menu>
          </template>
        </a-dropdown>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import TypePwd from '@/components/content/type/TypePwd'
import TypeImg from '@/components/content/type/TypeImg'
import { Modal } from 'ant-design-vue'
import ipc from '@/utils/ipc'
import TypeOther from "@/components/content/type/TypeOther";
import TypeVideo from "@/components/content/type/TypeVideo";
import TypeUrl from "@/components/content/type/TypeUrl";
export default {
  name: 'ContentItemList',
  components: { TypePwd, TypeImg ,TypeOther,TypeVideo,TypeUrl},
  props: {
  },
  data () {
    return {
      selectedKey: null,

    }
  },
  methods:{
    open:ipc.openExternal,
    showItemInFolder:ipc.showItemInFolder,
    selectItem(content){
      this.$store.commit('ui/focusItem',{
        type:'content',
            data:content
      })
    },
    openPath(content){
      ipc.openExternal(content.baseInfo.path+'/'+content.baseInfo.filename)
    },
    openDetail(content){
        if(content.type==='img') {
          this.$router.push({path: '/main/img/' + encodeURIComponent(content.baseInfo.path + '/' + content.baseInfo.filename)})
        }else if(content.type==='url') {
          ipc.addTab(content.href)

        }
else
            {
          return
        }
    },
    trashItem(path){
      let that=this
      Modal.confirm({
        title: '删除确认',
        content: '是否确认删除内容？此操作会删除内容的其他信息，此操作无法恢复。',
        okText: '确认',
        cancelText: '取消',
        onOk:()=>{
          ipc.trashItem(path)
          setTimeout(()=>{
            that.$store.dispatch('ui/changeTab',{name:'all'})
          },200)
          this.$message.success('删除内容成功。')
        }
      });
    },
    //设置新标签页壁纸
    setWallPaper(wallPaper){
      ipc.setWallPaper(wallPaper)
      this.$message.success('设置为桌面默认壁纸成功，如果当前桌面单独设置了壁纸，则仍然以原壁纸显示。未设置单独壁纸的桌面将以新壁纸显示。')
    },
    //通过ipc打开图片编辑器
    editPicture(src){
      ipc.handleFileAssign('image',{filePath:'file://'+src})
    }
  },
  computed: {

    ...mapState(
      {
        subFolders: state => state.subFolder.subFolders,
        itemSize: state => state.ui.itemSize,
        contents: state => state.content.contents
      }
    ),
    ...mapGetters('content',
      ['contentsOrderByCreateTime']
    )
  }
}
</script>

