import contentTypeHelper from "@/utils/contentTypeHelper";
import tools from "@/utils/tools";
import pathTools from "@/utils/pathTools";
import libDb from '@/utils/db/libDb'
const fs=eval('require')('fs')
const sizeOf =eval('require')('image-size')

const contentModel = {
    addLink(folder, title, url, cover,favicon, remark){
      return window.libDb.addUrl(folder, title, url, cover,favicon, remark)
    },
    getMetaFromFile(relativePath){
       relativePath=pathTools.convertToCommonRelativePath(relativePath)
       return window.libDb.getMeta(relativePath)
    },
    /**
     * 通过文件获得Content对象
     * @returns {{ext: (*|string|string|string), cover: string, baseInfo: {path, filename, modifyTime: T | T, addTime: T | T, size, createTime: T | T, sizeText: string, relativePath}, extData: {}, meta: (*|void), name, href: string, extCanOpenInside: boolean, type: (string|*)}}
     * @param relativeFullPath
     */
    getContentFromFile(relativeFullPath) {
        let fullPath=pathTools.getFullPathFromRelativeFullPath(relativeFullPath)
        let filename=pathTools.getFilenameFromRelativeFullPath(relativeFullPath)

        let path=pathTools.convertToCommonDir(relativeFullPath.substr(0,relativeFullPath.lastIndexOf('/')))
        let relativePath=pathTools.convertToCommonRelativeDir(path.replace(window.$defaultStrorePath,''))
        let fileInfo=fs.statSync(fullPath)
        const fileExt=filename.substr(filename.lastIndexOf('.'))

        const type = contentTypeHelper.getContentTypeNameFromExt(fileExt)
        let content = {
            extCanOpenInside: contentTypeHelper.extCanOpenInside(fileExt),
            name:  filename.substr(0,filename.lastIndexOf('.')), //文件名去除后缀，防止用户修改后缀
            type: type,
            ext: fileExt,
            extData: {
                // width: '900',
                // height: '420'
            },
            baseInfo: {
                path: path,
                relativePath: relativePath,
                filename: filename,
                createTime: fileInfo.birthtimeMs,
                addTime: fileInfo.ctimeMs,
                modifyTime: fileInfo.mtimeMs,
                size: fileInfo.size,
                sizeText: tools.convertToText(fileInfo.size)
            },
            href: '',
        }
        let meta= contentModel.getMetaFromFile(relativeFullPath)
        if(type==='img'){
            let dimensions=sizeOf(fullPath)
            content.cover=pathTools.convertToCommonRelativePath(relativeFullPath)
            content.extData.width=dimensions.width
            content.extData.height=dimensions.height
        }

        content=Object.assign(content,meta)
        content.extData=Object.assign(content.extData,content.ext_data)

        content.meta=meta
        return content
    },
    removeContent(path,isFullPath=true){
        let relativePath=path
        if(isFullPath){
            relativePath=pathTools.getRelativeFullPathFromFullPath(path)
        }
        libDb.removeMeta(relativePath)
    }
}

export default contentModel
